.item {
  display: flex;
  justify-content: space-evenly;
  margin: 15px;
  list-style: none;
  border: solid 1px rgb(211, 221, 221);
  box-shadow:  0 0 5px;
    
}  

.onLine,
.offLine {
    display: block;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgb(22, 216, 22);
    margin: 20px;
}

.offLine {
    background-color: rgb(235, 64, 64);
}

.avatar {
  margin-left: 20px;
}

.name {
  font-size: 24px;
  font-weight: 600;
  margin-top: 15px;
  flex-grow: 1;
  text-align: center;
}